import { useState, useEffect, useRef } from "react";
import { FaCog } from "react-icons/fa";
import { useRouter } from "next/navigation";
import ExportModal from "./ExportModal";

export type SidebarView = "calendar" | "report" | "user-report" | "dashboard";

type SidebarProps = {
  onSelectView: (view: SidebarView) => void;
};

export default function Sidebar({ onSelectView }: SidebarProps) {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isReportOpen, setIsReportOpen] = useState(false);
  const [isExportOpen, setIsExportOpen] = useState(false);
  const [isExportMyDataOpen, setIsExportMyDataOpen] = useState(false);
  const [userRole, setUserRole] = useState("");
  const router = useRouter();

  const reportRef = useRef<HTMLLIElement | null>(null);
  const menuRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const fetchUserRole = async () => {
      const userId = localStorage.getItem("userId");
      if (!userId) return;

      try {
        const response = await fetch(`/api/getUserRole?userId=${userId}`);
        const data = await response.json();

        if (data.role) {
          setUserRole(data.role);
        }
      } catch (error) {
        console.error("‚ùå „É¶„Éº„Ç∂„ÉºÂΩπËÅ∑„ÅÆÂèñÂæó„Ç®„É©„Éº:", error);
      }
    };

    fetchUserRole();
  }, []);

  const handleLogout = () => {
    localStorage.removeItem("isAuthenticated");
    router.push("/login");
  };

  const handleChangePassword = () => {
    router.push("/change-password");
  };

  const handleExport = async (startDate: string, endDate: string, format: string, userId?: string): Promise<boolean> => {
    console.log(`üì• „Ç®„ÇØ„Çπ„Éù„Éº„ÉàÈñãÂßã: ${startDate} ÔΩû ${endDate}, „Éï„Ç©„Éº„Éû„ÉÉ„Éà: ${format}, „É¶„Éº„Ç∂„ÉºID: ${userId || "ÂÖ®„Éá„Éº„Çø"}`);

    try {
      const response = await fetch("/api/exportData", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ startDate, endDate, format, userId }) // ‚úÖ `userId` „Åå„ÅÇ„ÇãÂ†¥Âêà„ÄÅËá™ÂàÜ„ÅÆ„Éá„Éº„Çø„ÅÆ„Åø
      });

      if (!response.ok) {
        const errorData = await response.json();
        if (errorData.error === "ÂØæË±°ÊúüÈñì„Å´Ë©≤ÂΩì„Éá„Éº„Çø„ÅØ„ÅÇ„Çä„Åæ„Åõ„Çì") {
          return false; // ‰øÆÊ≠£ÁÆáÊâÄ: „Éá„Éº„Çø„Åå„Å™„ÅÑÂ†¥Âêà„ÅØ `false` „ÇíËøî„Åô
        }
        throw new Error("„Ç®„ÇØ„Çπ„Éù„Éº„ÉàÂá¶ÁêÜ„Å´Â§±Êïó„Åó„Åæ„Åó„Åü");
      }

      const blob = await response.blob();
      const downloadUrl = window.URL.createObjectURL(blob);
      const link = document.createElement("a");
      link.href = downloadUrl;
      link.setAttribute("download", `export.${format}`);
      document.body.appendChild(link);
      link.click();
      link.remove();

      console.log("‚úÖ „Ç®„ÇØ„Çπ„Éù„Éº„ÉàÊàêÂäüÔºÅ");
      return true; // ‰øÆÊ≠£ÁÆáÊâÄ: „Ç®„ÇØ„Çπ„Éù„Éº„ÉàÊàêÂäüÊôÇ„Å´ `true` „ÇíËøî„Åô
    } catch (error) {
      console.error("‚ùå „Ç®„ÇØ„Çπ„Éù„Éº„Éà„Ç®„É©„Éº:", error);
      return false; // ‰øÆÊ≠£ÁÆáÊâÄ: „Ç®„É©„ÉºÊôÇ„ÅØ `false` „ÇíËøî„Åô
    }
  };

  // ‚úÖ Â§ñÈÉ®„ÇØ„É™„ÉÉ„ÇØ„Åß„Éõ„ÉÉ„Éó„Ç¢„ÉÉ„Éó„ÇíÈñâ„Åò„Çã
  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (reportRef.current && !reportRef.current.contains(event.target as Node)) {
        setIsReportOpen(false);
      }
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        setIsMenuOpen(false);
      }
    }

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  return (
    <div className="sidebar">
      <h1 className="sidebar-title">Âü∑ÂãôÁÆ°ÁêÜ„Ç∑„Çπ„ÉÜ„É†</h1>

      {/* üìå „É¨„Éù„Éº„Éà„É°„Éã„É•„Éº */}
      <ul>
      <li className="report-item" onClick={() => onSelectView("calendar")}>
       <span>üóìÔ∏è „Çø„Ç§„É†„Ç∑„Éº„Éà</span>
       <span className="report-arrow"></span>
     </li>
        
        <li 
          onClick={() => setIsReportOpen(!isReportOpen)}
          ref={reportRef}
        >
          
            <span>üìë„É¨„Éù„Éº„Éà</span>
            <span className="report-arrow">‚ñ∂</span>
          
          

          {isReportOpen && (
            <div className="menu-popup_report">
              {/* ÂÖ®„É¶„Éº„Ç∂„Éº„Åå‰ΩøÁî®ÂèØËÉΩ„Å™ExportÔºàMy DataÔºâ */}
              <button
                className="export-button"
                onClick={() => setIsExportMyDataOpen(true)}
              >
                üì§ ExportÔºàMy DataÔºâ
              </button>

              {isExportMyDataOpen && (
               <ExportModal 
                 isOpen={isExportMyDataOpen}
                 onClose={() => setIsExportMyDataOpen(false)}
                 onExport={async (startDate, endDate, format) => {
                  const userId = localStorage.getItem("userId") || ""; // üîπ „É¶„Éº„Ç∂„ÉºID„ÇíÂèñÂæó
                  return await handleExport(startDate, endDate, format, userId); // üîπ `await` „ÇíËøΩÂä†„Åó `Promise<boolean>` „ÇíËøî„Åô
                }}
               />
              )}

              
              
              {/* ÁÆ°ÁêÜËÄÖ„ÅÆ„ÅøË°®Á§∫„Åï„Çå„ÇãExportÔºàall DataÔºâ */}
              {userRole === "ÁÆ°ÁêÜËÄÖ" && (
              <div>
              <button
                  className="export-button"
                  onClick={() => setIsExportOpen(true)}
                >
                  üì§ ExportÔºàall DataÔºâ
                </button>

                <button
                className="export-button"
                onClick={() => onSelectView("report")}
              >
              üìä ‰∫àÂÆü„É¨„Éù„Éº„Éà
              </button>

              <button
               className="export-button"
               onClick={() => onSelectView("user-report")}
              >
               üë§ „É¶„Éº„Ç∂„ÉºÂà•„É¨„Éù„Éº„Éà
              </button>
              <button className="export-button" onClick={() => onSelectView("dashboard")}>
                üìà „É¶„Éº„Ç∂„ÉºÊúàÊ¨°„ÉÄ„ÉÉ„Ç∑„É•„Éú„Éº„Éâ
              </button>
              </div>
              )}



            </div>
          )}
        </li>
      </ul>

      {/* Export„ÅÆ„É¢„Éº„ÉÄ„É´ */}
      {isExportMyDataOpen && (
        <ExportModal 
          isOpen={isExportMyDataOpen}
          onClose={() => setIsExportMyDataOpen(false)}
          onExport={async (startDate, endDate, format) => {
            const userId = localStorage.getItem("userId") || ""; // üîπ „É¶„Éº„Ç∂„ÉºID„ÇíÂèñÂæó
            return await handleExport(startDate, endDate, format, userId); // üîπ `await` „ÇíËøΩÂä†„Åó `Promise<boolean>` „ÇíËøî„Åô
          }}
        />
      )}

      {isExportOpen && (
        <ExportModal 
          isOpen={isExportOpen}
          onClose={() => setIsExportOpen(false)}
          onExport={(startDate, endDate, format) => handleExport(startDate, endDate, format)}
        />
      )}

      {/* üìå ÁÆ°ÁêÜ„É°„Éã„É•„Éº */}
      <div 
        className="settings"
        onClick={() => setIsMenuOpen(!isMenuOpen)}
        ref={menuRef}
      >
        <FaCog size={20} />
        <span>ÁÆ°ÁêÜ„É°„Éã„É•„Éº</span>
        {isMenuOpen && (
          <div className="menu-popup">
            <button className="pw-change-button" onClick={handleChangePassword}>
              üîë PWÂ§âÊõ¥
            </button>

            <button className="logout-button" onClick={handleLogout}>
              „É≠„Ç∞„Ç¢„Ç¶„Éà
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
