"use client";
import { useState, useEffect } from "react";
import { EventData } from "@/types/EventData"; // ‚úÖ `EventData` „ÇíÊ≠£„Åó„Åè„Ç§„É≥„Éù„Éº„Éà
import Select from "react-select";

interface ModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (data: EventData) => Promise<void>;
  onUpdate: (data: EventData, range: { start: Date; end: Date } | null) => Promise<void>;
  onDelete: (id: string) => Promise<void>; // ‚úÖ ÂâäÈô§Áî®„ÅÆÈñ¢Êï∞„ÇíËøΩÂä†
  selectedRange?: { start: Date; end: Date } | null;
  selectedEvent?: EventData | null;
}

interface Engagement {
  id: string;
  name: string;
}

export function Modal({ isOpen, onClose, onSubmit, onUpdate, onDelete, selectedRange, selectedEvent }: ModalProps) {
  const [engagement, setEngagement] = useState<{ value: string; label: string } | null>(null);
  const [engagements, setEngagements] = useState<Engagement[]>([]);
  const [activity, setActivity] = useState("");
  const [activities, setActivities] = useState<{ engagement: string; activity_id: string; activity: string }[]>([]);
  const [filteredActivities, setFilteredActivities] = useState<{ activity_id: string; activity: string }[]>([]);
  const [location, setLocation] = useState<{ value: string; label: string } | null>(null);
  const [locations, setLocations] = useState<{ value: string; label: string }[]>([]);
  const [details, setDetails] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false); 
  const [isDuplicating, setIsDuplicating] = useState(false);

  const handleAction = async () => {
    console.log("‚úÖ ËøΩÂä†„ÉªÊõ¥Êñ∞„Éú„Çø„É≥„ÅåÊäº„Åï„Çå„Åæ„Åó„ÅüÔºÅ");
    setIsSubmitting(true); // üî∏ÈÄÅ‰ø°ÈñãÂßãÊôÇ„Å´„É≠„ÉÉ„ÇØ

    try {
      if (selectedEvent) {
        if (!selectedRange) {
          console.error("‚ùå Êõ¥Êñ∞ÁØÑÂõ≤„ÅåÊú™ÂÆöÁæ©„Åß„ÅôÔºÅ");
          return;
        }

        await onUpdate({
          id: selectedEvent.id || "",
          userId: localStorage.getItem("userId") || "",
          engagement: engagement?.value ?? "",
          activity,
          location: location?.value || "",
          details,
          start: selectedRange.start.toISOString(),
          end: selectedRange.end.toISOString(),
        }, selectedRange);
      } else {
        await onSubmit({
          id: "",
          userId: localStorage.getItem("userId") || "",
          engagement: engagement?.value ?? "",
          activity,
          location: location?.value || "",
          details,
          start: selectedRange?.start.toISOString() || "",
          end: selectedRange?.end.toISOString() || "",
        });
      }
    } finally {
      setIsSubmitting(false); // üî∏ÈÄÅ‰ø°Âæå„Å´Ëß£Èô§
    }
  };


  // ‚úÖ „Ç®„É≥„Ç≤„Éº„Ç∏„É°„É≥„Éà„É™„Çπ„Éà„ÅÆÂèñÂæó
  useEffect(() => {
    const fetchEngagements = async () => {
      try {
        const userId = typeof window !== "undefined" ? localStorage.getItem("userId") : null;
        if (!userId) {
          console.error("‚ùå „É¶„Éº„Ç∂„ÉºID„ÅåÂèñÂæó„Åß„Åç„Åæ„Åõ„ÇìÔºÅ");
          return;
        }
  
        const response = await fetch(`/api/fetchEngagements?userId=${encodeURIComponent(userId)}`);
        if (!response.ok) throw new Error("„Ç®„É≥„Ç≤„Éº„Ç∏„É°„É≥„Éà„ÅÆÂèñÂæó„Å´Â§±Êïó„Åó„Åæ„Åó„Åü");
  
        const data = await response.json();
        if (!Array.isArray(data)) {
          console.error("‚ùå API„ÅÆ„É¨„Çπ„Éù„É≥„Çπ„ÅåÈÖçÂàó„Åß„ÅØ„ÅÇ„Çä„Åæ„Åõ„Çì:", data);
          return;
        }
  
        const formattedEngagements = data.map((eng: { id?: string; name: string }, index: number) => ({
          id: eng.id || index.toString(),
          name: eng.name,
        }));
  
        setEngagements(formattedEngagements);
      } catch (error) {
        console.error("‚ùå „Ç®„É≥„Ç≤„Éº„Ç∏„É°„É≥„Éà„ÅÆÂèñÂæó„Ç®„É©„Éº:", error);
      }
    };
  
    if (isOpen) {
      fetchEngagements();
    }
  }, [isOpen]);
  


// ‚úÖ Activities „ÇíÂèñÂæó
useEffect(() => {
  const fetchActivities = async () => {
    try {
      const response = await fetch("/api/fetchActivities");
      const data = await response.json();
      console.log("‚úÖ Activity „Éá„Éº„Çø:", data);
      setActivities(data);
    } catch (error) {
      console.error("‚ùå Activity „ÅÆÂèñÂæó„Ç®„É©„Éº:", error);
    }
  };

  if (isOpen) {
    fetchActivities();
  }
}, [isOpen]);

// ‚úÖ Engagement „Å´Âøú„Åò„Åü Activity „Çí„Éï„Ç£„É´„Çø„É™„É≥„Ç∞
useEffect(() => {
  const filtered = activities.filter(act => act.engagement === engagement?.value);

  setFilteredActivities(filtered);
}, [engagement, activities]);
  

  // ‚úÖ `selectedEvent` „ÅÆ„Éá„Éº„Çø„Çí„Çª„ÉÉ„Éà
  useEffect(() => {
    if (selectedEvent) {
      console.log("üìå ÈÅ∏Êäû„Åï„Çå„Åü„Ç§„Éô„É≥„Éà:", selectedEvent);
      setEngagement(selectedEvent.engagement ? { value: selectedEvent.engagement, label: selectedEvent.engagement } : null);
      setActivity(selectedEvent.activity || "");
      setLocation(selectedEvent.location ? { value: selectedEvent.location, label: selectedEvent.location } : null);
      setDetails(selectedEvent.details || "");
    } else {
      // Êñ∞Ë¶è‰ΩúÊàêÊôÇ„ÅÆ„Éá„Éï„Ç©„É´„ÉàÂÄ§
      setEngagement(null);
      setActivity("");
      setLocation(null);
      setDetails("");
    }
  }, [selectedEvent, engagements]);

  // üîπ ‰ΩúÊ•≠Â†¥ÊâÄ„É™„Çπ„Éà„ÅÆÂèñÂæó
  useEffect(() => {
    const fetchLocations = async () => {
      try {
        const response = await fetch("/api/fetchLocations");
        const data = await response.json();
        console.log("‚úÖ ÂèñÂæó„Åó„Åü‰ΩúÊ•≠Â†¥ÊâÄ:", data);
        setLocations(data);
      } catch (error) {
        console.error("‚ùå ‰ΩúÊ•≠Â†¥ÊâÄ„ÅÆÂèñÂæó„Ç®„É©„Éº:", error);
      }
    };

    if (isOpen) {
      fetchLocations();
    }
  }, [isOpen]);

  if (!isOpen) return null;

  return (
    <div className="modal">
      <div className="modal-content">
        <h1>‰ΩúÊ•≠ÊÉÖÂ†±„ÇíÂÖ•Âäõ</h1>
        {selectedRange && (
         <p>
          üìÖ {new Date(selectedRange.start.getTime() - 9 * 60 * 60 * 1000).toLocaleTimeString("ja-JP", { hour: "2-digit", minute: "2-digit" })} 
           ÔΩû {new Date(selectedRange.end.getTime() - 9 * 60 * 60 * 1000).toLocaleTimeString("ja-JP", { hour: "2-digit", minute: "2-digit" })}
         </p>
        )}


         {/* „Ç®„É≥„Ç≤„Éº„Ç∏„É°„É≥„ÉàÈÅ∏Êäû */}
        <div style={{ marginBottom: "10px" }}>
          <label>„Ç®„É≥„Ç≤„Éº„Ç∏„É°„É≥„Éà</label><br />
          <Select
            options={engagements.map(eng => ({
            value: eng.name,
            label: eng.name
            }))}
            value={engagement}
            onChange={(selectedOption) => setEngagement(selectedOption)}
            styles={{
              menuList: (provided) => ({
                ...provided,
                maxHeight: "200px", // ÊúÄÂ§ß5„Å§„Åæ„ÅßË°®Á§∫
                overflowY: "auto",
              }),
              menu: (provided) => ({
                ...provided,
                zIndex: 9999, // ÂâçÈù¢„Å´Ë°®Á§∫
              })
            }}
            placeholder="„Ç®„É≥„Ç≤„Éº„Ç∏„É°„É≥„Éà„ÇíÈÅ∏Êäû"
          />

        </div >


        {/* Activity „ÅÆÈÅ∏Êäû (react-select) */}
        <div style={{ marginBottom: "10px" }}>
        <label>Activity</label><br />
        <Select
          options={filteredActivities.map(act => ({
            value: act.activity_id, // activity_id „Çí value „Å´Ë®≠ÂÆö
            label: act.activity     // Ë°®Á§∫„Åï„Çå„Çã„É©„Éô„É´
          }))}
          value={filteredActivities.find(act => act.activity === activity) 
            ? { value: activity, label: activity } 
            : null}
          onChange={(selectedOption) => setActivity(selectedOption?.label || "")}
          styles={{
            menuList: (provided) => ({
              ...provided,
              maxHeight: "200px", // 5„Å§„Åæ„ÅßË°®Á§∫
              overflowY: "auto",
            }),
            menu: (provided) => ({
              ...provided,
              zIndex: 9999, // ‰ªñ„ÅÆË¶ÅÁ¥†„Çà„ÇäÂâçÈù¢„Å´Ë°®Á§∫
            })
          }}
          placeholder="Activity„ÇíÈÅ∏Êäû"
        />
        </div>    

        <label>‰ΩúÊ•≠Â†¥ÊâÄ</label>
        <Select
            options={locations}
            value={location} // ‚úÖ location „ÅÆÂûã„Çí `{ value: string; label: string } | null` „Å´Áµ±‰∏Ä
            onChange={(selectedOption) => setLocation(selectedOption)} // ‚úÖ „Åù„ÅÆ„Åæ„Åæ„Çª„ÉÉ„Éà
            placeholder="‰ΩúÊ•≠Â†¥ÊâÄ„ÇíÈÅ∏Êäû"
            styles={{
              menuList: (provided) => ({
                ...provided,
                maxHeight: "200px", // ÊúÄÂ§ß5„Å§„Åæ„ÅßË°®Á§∫
                overflowY: "auto",
              }),
              menu: (provided) => ({
                ...provided,
                zIndex: 9999, // ÂâçÈù¢„Å´Ë°®Á§∫
              }),
            }}
        />


        <label style={{ display: "block", marginTop:"14px",marginBottom: "-15px" }}>
          ‰ΩúÊ•≠ÂÜÖÂÆπ
        </label>
        
        <input
           type="text"
           value={details}
           onChange={(e) => setDetails(e.target.value)}
           style={{
             paddingTop: "5px", // üîπ ÊñáÂ≠ó„ÇíÂ∞ë„Åó‰∏ã„Å´„Åö„Çâ„Åô
             paddingBottom: "5px",
             height: "30px", // ‚úÖ ÈÅ©Â∫¶„Å™È´ò„Åï„ÇíË®≠ÂÆö
          }}
        /> 




<button
  onClick={handleAction}
  disabled={isSubmitting}
  style={{
    opacity: isSubmitting ? 0.6 : 1,
    pointerEvents: isSubmitting ? "none" : "auto",
  }}
>
  {isSubmitting ? "ÈÄÅ‰ø°‰∏≠..." : selectedEvent ? "Êõ¥Êñ∞" : "ËøΩÂä†"}
</button>


{/* ‚úÖ Ë§áË£Ω„Éú„Çø„É≥„ÅÆËøΩÂä†ÔºàselectedEvent„Åå„ÅÇ„Çã„Å®„Åç„ÅÆ„ÅøË°®Á§∫Ôºâ */}
{selectedEvent && (
  <button
    onClick={async () => {
      if (isDuplicating) return;
      setIsDuplicating(true);

      console.log("üìã Ë§áË£Ω„Éú„Çø„É≥„ÅåÊäº„Åï„Çå„Åæ„Åó„ÅüÔºÅ");
      const duplicatedStart = selectedRange?.start?.toISOString() || "";
      const duplicatedEnd = selectedRange?.end?.toISOString() || "";

      try {
        await onSubmit({
          id: "", // Êñ∞„Åó„ÅÑID
          userId: localStorage.getItem("userId") || "",
          engagement: engagement?.value ?? "",
          activity,
          location: location?.value || "",
          details,
          start: duplicatedStart,
          end: duplicatedEnd,
        });
      } catch (error) {
        console.error("‚ùå Ë§áË£Ω„Ç®„É©„Éº:", error);
      } finally {
        setIsDuplicating(false);
      }
    }}
    disabled={isDuplicating}
    style={{
      marginLeft: "10px",
      backgroundColor: "darkorange",
      color: "white",
      opacity: isDuplicating ? 0.6 : 1,
      pointerEvents: isDuplicating ? "none" : "auto",
    }}
  >
    {isDuplicating ? "Ë§áË£Ω‰∏≠..." : "Ë§áË£Ω"}
  </button>
)}


{/* ‚úÖ ‰∫àÂÆö„Åå„ÅÇ„Çã„Å®„Åç„ÅÆ„Åø„ÄåÂâäÈô§„Äç„Éú„Çø„É≥„ÇíË°®Á§∫ */}
{selectedEvent && (
  <button
    onClick={async () => {
      if (isDeleting) return;
      setIsDeleting(true);

      console.log("üóëÔ∏è ÂâäÈô§„Éú„Çø„É≥„ÅåÊäº„Åï„Çå„Åæ„Åó„ÅüÔºÅ");
      try {
        await onDelete(selectedEvent.id);
      } catch (error) {
        console.error("‚ùå ÂâäÈô§„Ç®„É©„Éº:", error);
      } finally {
        setIsDeleting(false);
      }
    }}
    disabled={isDeleting}
    style={{
      backgroundColor: "red",
      color: "white",
      marginLeft: "10px",
      opacity: isDeleting ? 0.6 : 1,
      pointerEvents: isDeleting ? "none" : "auto",
    }}
  >
    {isDeleting ? "ÂâäÈô§‰∏≠..." : "ÂâäÈô§"}
  </button>
)}






        <button onClick={onClose}>„Ç≠„É£„É≥„Çª„É´</button>
      </div>
    </div>
  );
}
